export interface RxRating {
  fill: number;
  index: number;
}

export enum RxRatingSize {
  xs = 0,
  sm = 1,
  md = 2,
  lg = 3
}

export enum RxRatingBinaryType {
  Icon = 0,
  Text = 1,
  Count = 2
}

export enum RxRatingBinaryAction {
  Like = 0,
  Dislike = 1,
  Increase = 2,
  Decrease = 3,
  Undo = 4,
}

export interface RxRatingBinaryChangeEvent {
  type: RxRatingBinaryAction;
  value: number | null;
}

export enum RxRatingLikeScale {
  ScalePoint5 = 0,
  ScalePoint3 = 1
}

export enum RxRatingLikeItem {
  Terrible = 0,
  Disliked = 1,
  Ok = 2,
  Good = 3,
  Excellent = 4
}

export enum RxRatingVotingType {
  Star = 0,
  Flag = 1,
  Like = 2,
  Thanks = 3
}

// TEXTS
export interface RxRatingTexts {
  tooltip?: string[];
}

export interface RxRatingBinaryTexts {
  btnTextLike?: string;
  btnTextDislike?: string;
  btnTextUndo?: string;
  quotesLeft?: string;
  quotesRight?: string;
  likeMessage?: string;
  dislikeMessage?: string;
  textMessage?: string;
}

export interface RxRatingLikeTexts {
  tooltipTerrible?: string;
  tooltipDisliked?: string;
  tooltipOk?: string;
  tooltipGood?: string;
  tooltipExcellent?: string;
}

export interface RxRatingVotingTexts {
  thanksText?: string;
  thanksAbbrMessage?: string;
}

export interface RxRatingConfigTexts {
  rating: RxRatingTexts;
  binary: RxRatingBinaryTexts;
  like: RxRatingLikeTexts;
  voting: RxRatingVotingTexts;
}
